<template>
  <div class="songs-container">
    <div class="tab-bar">
      <!-- 修改高亮 -->
      <span class="item" :class="{active:tag=='0'}" @click="tag=0">全部</span>
      <span class="item" :class="{active:tag=='7'}" @click="tag=7">华语</span>
      <span class="item" :class="{active:tag=='96'}" @click="tag=96">欧美</span>
      <span class="item" :class="{active:tag=='8'}" @click="tag=8">日本</span>
      <span class="item" :class="{active:tag=='16'}" @click="tag=16">韩国</span>
    </div>
    <!-- 底部的table -->
    <table class="el-table playlit-table">
      <thead>
        <th></th>
        <th></th>
        <th>音乐标题</th>
        <th>歌手</th>
        <th>专辑</th>
        <th>时长</th>
      </thead>
      <tbody>
        <tr v-for="(item,index) in Lists" :key="index" class="el-table__row">
          <td>{{index+1}}</td>
          <td>
            <div class="img-wrap">
              <img :src="item.album.picUrl" alt="" />
              <!-- 播放按钮，播放的接口加上id就可以直接播放 -->
              <span @click="playMusic(item.id)" class="iconfont icon-play"></span>
            </div>
          </td>
          <td>
            <div class="song-wrap">
              <div class="name-wrap">
                <span>{{item.name}}</span>
                <span class="iconfont icon-mv"></span>
              </div>
              <span></span>
            </div>
          </td>
          <td>{{item.album.artists["0"].name}}</td>
          <td>{{item.album.name}}</td>
          <td>{{item.duration}}</td>
        </tr>
        
        
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name:'songs',
  data() {
    return {
      // 歌曲列表
        Lists:[],
        //分类，默认全部
        tag:"7"
    }
  },
  watch:{
    tag(){
      this.getters()
    }},
  methods:{
    playMusic(id){
      axios({
        url:'https://autumnfish.cn/song/url',
    method:'get',
    params:{id:id}
      }).then(res =>{
        console.log(res)
        let url=res.data.data[0].url
        this.$parent.musicUrl=url
      })
    },
    getters(){
       axios({
     url:'https://autumnfish.cn/top/song',
      mothod:'get',
      params:{
// type调用此接口可以获得最新的音乐全部0，华语7，欧美96，日本8，韩国16
        //然后把这个接口设置为动态的
        type:this.tag
      }
    }).then(res =>{
        console.log(res)
        this.Lists=res.data.data
        //处理时长 把毫秒转为分秒，给lists每一个都循环
// 要修改每一项里面的duration时长，用for遍历每一个，
        for(let i =0;i<this.Lists.length;i++){
           //获取 当前每一项的秒数
         let duration= this.Lists[i].duration
//1毫秒等于0.1秒，假如350750毫秒就是除以1000等于350.75秒，在转换成分就350.75/62
        let min = parseInt( duration/1000/60)//这算的是分钟，parseInt是取整数
        if(min<10){
            min='0'+min
        }
        let sec = parseInt(duration/1000%60)
        if(sec<10){
          sec:'0'+sec
        }
        // console.log(min+':'+sec)
        this.Lists[i].duration=min+':'+sec
        }
    })
    }
  }
  ,
  created(){
    this.getters()
  }
};
</script>

