<template>
  <div class="discovery-container">
    <!-- 轮播图 -->
    <el-carousel class="" :interval="4000" type="card">
      <el-carousel-item v-for="(item,index) in banners" :key="index" >
        <img :src="item.imageUrl" alt="" />
      </el-carousel-item>
    </el-carousel>
    <!-- 推荐歌单 -->
    <div class="recommend">
      <h3 class="title">
        推荐歌单
      </h3>
      <div class="items">
        <div class="item" v-for='(item,index) in list' :key="index"  @click=" toplaylist(item.id)">
          <div class="img-wrap">
            <div class="desc-wrap">
              <span class="desc">{{item.copywriter}}</span>
            </div>
            <img :src="item.picUrl" alt="" />
            <span class="iconfont icon-play"></span>
          </div>
          <p class="name">{{item.name}}</p>
        </div>
       
      </div>
    </div>
    <!-- 最新音乐 -->
    <div class="news">
      <h3 class="title">
        最新音乐
      </h3>
      <div class="items">
        <div class="item"  v-for="(item,index) in music" :key='index'>
          <div class="img-wrap">
            <img :src="item.picUrl" alt="" />
            <span @click="playMusic(item.id)" class="iconfont icon-play"></span>
          </div> 
          <div class="song-wrap">
            <div class="song-name">{{item.name}}</div>
            <div class="singer">{{item.song.artists[0].name}}</div>
          </div>
        </div>
       
    
      </div>
    </div>
    <!-- 推荐MV -->
    <div class="mvs">
      <h3 class="title">推荐MV</h3>
      <div class="items">
        <div class="item" v-for="(item,index) in mv" :key="index" @click="toplaymv(item.id)">
          <div class="img-wrap">
            <img :src="item.picUrl" alt="" />
            <span class="iconfont icon-play"></span>
            <div class="num-wrap">
              <div class="iconfont icon-play"></div>
              <div class="num">{{item.playCount}}</div>
            </div>
          </div>
          <div class="info-wrap">
            <div class="name">{{item.name}}</div>
            <div class="singer">{{item.artistName}}</div>
          </div>
        </div>
     
      </div>
    </div>
  </div>
</template>

<script>
// 导入axios然后使用他的数据
import axios from 'axios'
export default {
  name:'discovery',
  data(){
    return {
      banners:[],
      list:[],
      music:[],
      mv:[]
    }
    
  },
  methods:{
    toplaylist(id){              //把id就传过去了
this.$router.push('/playlist?hzx='+id)
    },
    toplaymv(id){
      this.$router.push('/mv?hzx='+id)
    }
  ,
     playMusic(id){
      //  console.log(id)
       //点击一下就触发axios的执行事件，
       axios({///这个播放接口需要传id，给网站后面添加id，然后不一样的歌点后就会播放不一样的地址
         url:'https://autumnfish.cn/song/url',
         method:'get',
         params:{
           id:id//添加id到网站，两个id一样的话就可以只写一个
         }
       }).then(res => {
         //用一个参数来接收播放地址，然后在修改父组件里面的播放地址
          // console.log(res)//这里就可以看到播放地址了
          let url = res.data.data[0].url
          // console.log(this.$parent.musicUrl)
          //设置给父组件的播放地址
          this.$parent.musicUrl = url
       })
     }
  },
  created(){
    // console.log('xx')
    //轮播图获取到的数据
    axios({
      url:'https://autumnfish.cn/banner',
      mothod:'get',
    }).then(res=>{
      // console.log(res)
      this.banners = res.data.banners
    })
    //推荐歌单获取到的数据
    axios({
      url:'https://autumnfish.cn/personalized',
      method:'get',
      params:{
        limit:10//设置数量
      }
    }).then( res => {
      // console.log(res)
      this.list=res.data.result
    }),
    //最新音乐
    axios({
      url:'https://autumnfish.cn/personalized/newsong',
      method:'get',

    }).then(res=>{
      this.music=res.data.result
     
    }),
    //mv
    axios({
      url:'https://autumnfish.cn/personalized/mv',
      method:'get'
    }).then(res => {
      this.mv=res.data.result
      // console.log(res)
    })
  }
};
</script>

