<template>
  <div class="playlist-container">
    <div class="top-wrap">
      <div class="img-wrap">
        <!-- 封面 -->
        <img :src="playlist.coverImgUrl" alt="" />
      </div>
      <div class="info-wrap">
        <!-- 封面名字 -->
        <p class="title">{{playlist.name}}</p>
        <div class="author-wrap">
          <!-- 头像 -->
          <img class="avatar" :src="playlist.creator.avatarUrl" alt="" />
          <span class="name">{{playlist.creator.nickname}}</span>
          <span class="time">{{playlist.createTime}}</span>
        </div>
        <div class="play-wrap">
          <span class="iconfont icon-circle-play"></span>
          <span class="text" >播放全部</span>
        </div>
        <div class="tag-wrap">
          <!-- 分类标签 -->
          <span class="title">标签:</span>
          <ul>
            <!-- 因为他的标签在tage里面，数据里的标签有几个就遍历几个 -->
            <li v-for="(item,index) in playlist.tags" :key="index">{{item}}</li>
            
          </ul>
        </div>
        <div class="desc-wrap">
          <span class="title">简介:</span>
          <span class="desc">
             {{playlist.description}}
          </span>
         
          
        </div>
      </div>
    </div>
    <el-tabs v-model="activeIndex">
      <el-tab-pane label="歌曲列表" name="1">
        <table class="el-table playlit-table">
          <thead>
            <th></th>
            <th></th>
            <th>音乐标题</th>
            <th>歌手</th>
            <th>专辑</th>
            <th>时长</th>
          </thead>
          <tbody>
            <tr class="el-table__row" v-for="(item,index) in playlist.tracks" :key="index" @click="playMusic(item.id)">
              <td>{{index+1 }}</td>
              <td>
                <div class="img-wrap">
                  <img :src="item.al.picUrl" alt="" />
                  <span class="iconfont icon-play"></span>
                </div>
              </td>
              <td>
                <div class="song-wrap">
                  <div class="name-wrap">
                    <span v-if="item.value!=0">{{item.name}}</span>
                    <span  v-if="item.mv !== 0" class="iconfont icon-mv"></span>
                  </div>
                  <span>{{item.alia[0]}}</span>
                </div>
              </td>
               <td>{{item.ar[0].name}}</td>
              <td>{{item.al.name}}</td>
              <td>{{item.dt}}</td>
            </tr>
            
          </tbody>
        </table>
      </el-tab-pane>
      <el-tab-pane :label="'评论'+(TotalSum)" name="2">
        <!-- 精彩评论 -->
        <div class="comment-wrap">
          <p class="title">精彩评论<span class="number">{{hotCount}}</span></p>
          <div class="comments-wrap">
            <div class="item" v-for="(item,index) in hotComment" :key="index">
              <div class="icon-wrap">
                <img :src="item.user.avatarUrl" alt="" />
              </div>
              <div class="content-wrap">
                <div class="content">
                  <span class="name">{{item.user.nickname}}：</span>
                  <span class="comment">{{item.content}}</span>
                </div>
                <div class="re-content" v-if="item.beReplied.length!=0">
                  <span class="name">{{item.beReplied[0].user.nickname}}：</span>
                  <span class="comment">{{item.beReplied[0].content}}</span>
                </div>
                <div class="date">2020-02-12 17:26:11</div>
              </div>
            </div>
          </div>
        </div>
        <!-- 最新评论 -->
        <div class="comment-wrap">
          <p class="title">最新评论<span class="number">{{total}}</span></p>
          <div class="comments-wrap">
            <div class="item"  v-for="(item,index) in comments" :key="index">
              <div class="icon-wrap">
                <img :src="item.user.avatarUrl" alt="" />
              </div>
              <div class="content-wrap">
                <div class="content" >
                  <span class="name">{{item.user.nickname}}：</span>
                  <span class="comment">{{item.content}}</span>
                </div>
                <div class="re-content" v-if="item.beReplied.length!=0">
                  <span class="name">{{item.beReplied[0].user.nickname}}：</span>
                  <span class="comment">{{item.beReplied[0].content}}</span>
                </div>
                <div class="date">2020-02-12 17:26:11</div>
              </div>
            </div>
            
            
          </div>
        </div>
        <!-- 分页器 -->
        <el-pagination
          @current-change="handleCurrentChange"
          background
          layout="prev, pager, next"
          :total="total"
          :current-page="page"
          :page-size="5"
        >
        </el-pagination>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'playlist',
  data() {
    return {
      activeIndex: '1',
      // 总条数
      total: 0,
      // 页码
      page: 1,
      playlist:{},//因为封面就只有一组数据所以就用{}，多的话就要数组[ ]
    hotComment:[],
    hotCount:0,
    comments:[],
    TotalSum:0,
    };
  },
  methods: {
     toplaylist(id){              //把id就传过去了
this.$router.push('/playlist?hzx='+id)
    },
     playMusic(id){
      axios({
        url:'https://autumnfish.cn/song/url',
    method:'get',
    params:{id:id}
      }).then(res =>{
        // console.log(res)
        let url=res.data.data[0].url
        this.$parent.musicUrl=url
      })
    },
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`);
      this.page=val
       axios({
        url:'https://autumnfish.cn/comment/playlist',
        method:'get',
        params:{
          id:this.$route.query.hzx,
          limit:10,
          offset:(this.page-1)*10
        }}).then(res=>{
          //  console.log(res)
          this.total=res.data.total
          this.comments=res.data.comments
        })
      
      
    }
  },
  created(){
    axios({
      url:'https://autumnfish.cn/playlist/detail',
      method:'get',
      params:{
        id:this.$route.query.hzx//我id存放的值就在$router.query.hzx里面
      }
    }).then(res=>{
      // console.log(res)
      this.playlist=res.data.playlist
      
      // this.idd=res.data.playlist.id
       for (let i = 0;i < this.playlist.length; i++) {
          console.log(this.playlist.dt)
          let min = parseInt(this.playlist[i].dt / 1000 / 60)
           let sec = parseInt((this.playlist[i].dt / 1000) % 60)
           if (min < 10) {
            min = '0' + min
          }
          if(sec<10){
            sec = '0'+ sec
          }
         this.playlist[i].dt=min+':'+sec
      }
      //评论
      axios({
        url:'https://autumnfish.cn/comment/hot',
        method:'get',
        params:{
          id:this.$route.query.hzx,
          type:2
        }
      }).then(res=>{
        // console.log(res)
        // console.log(1)
        this.hotComment=res.data.hotComments
        this.hotCount=res.data.total
      })
      //获取最新评论
      axios({
        url:'https://autumnfish.cn/comment/playlist',
        method:'get',
        params:{
          id:this.$route.query.hzx,
          limit:1,
          offset:0
        }}).then(res=>{
         
          this.total=res.data.total
          this.comments=res.data.comments
           this.TotalSum=this.hotCount+this.total
        
      })
    })
  }
};
</script>

