<template>
  <div class="mvs-container">
    <div class="filter-wrap">
      <div class="seciton-wrap">
        <span class="section-type">地区:</span>
        <ul class="tabs-wrap">
          <li class="tab">
            <span class="title" :class="{active:area=='全部'}" @click="area='全部'">全部</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:area=='内地'}" @click="area='内地'">内地</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:area=='港台'}" @click="area='港台'">港台</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:area=='欧美'}" @click="area='欧美'">欧美</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:area=='日本'}" @click="area='日本'">日本</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:area=='韩国'}" @click="area='韩国'">韩国</span>
          </li>
        </ul>
      </div>
      <div class="type-wrap">
        <span class="type-type">类型:</span>
        <ul class="tabs-wrap">
          <li class="tab">
            <span class="title " :class="{active:type=='全部'}" @click="type='全部'">全部</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:type=='官方版'}" @click="type='官方版'">官方版</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:type=='原声'}" @click="type='原声'">原声</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:type=='现场版'}" @click="type='现场版'">现场版</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:type=='正先出品'}" @click="type='正先出品'">正先出品</span>
          </li>
        </ul>
      </div>
      <div class="order-wrap">
        <span class="order-type">排序:</span>
        <ul class="tabs-wrap">
          <li class="tab">
            <span class="title " :class="{active:order=='上升最快'}" @click="order='上升最快'">上升最快</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:order=='最热'}" @click="order='最热'">最热</span>
          </li>
          <li class="tab">
            <span class="title" :class="{active:order=='最新'}" @click="order='最新'">最新</span>
          </li>  <li class="tab">
            <span class="title" :class="{active:order=='我要自闭了啊~~'}" @click="order='我要自闭了啊~~'">我要自闭了啊~~</span>
          </li>
        </ul>
      </div>
    </div>
    <!-- mv容器 -->
    <!-- 推荐MV -->
    <div class="mvs">
      <div class="items">
        <div class="item" v-for="(item,index) in list" :key="index"  @click=" toplaylist(item.id)" >
          <div class="img-wrap">
            <img :src="item.cover" alt="" />
            <div class="num-wrap">
              <div class="iconfont icon-play"></div>
              <div class="num">{{item.playCount}}</div>
            </div>
          </div>
          <div class="info-wrap">
            <div class="name">{{item.name}}</div>
            <div class="singer">{{item.artistName}}</div>
          </div>
        </div>
       </div>
      <!-- 分页器 -->
      <el-pagination
        @current-change="handleCurrentChange"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="page"
        :page-size="5"
        :limit="limit"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'mvs',
  data() {
    return {
      // 总条数
      total: 20,
      // 页码
      page: 1,
      // 页容量
      limit: 12,
      // 地区
      area:"全部",
      // 类型
      type:"全部",
      // 排序
      order:"上升最快",
      list:[]
    };
  },
  watch:{
    area(){
       this.getlist()
//当点击每个导航栏的时候再把当前的页面改成第一页
       this.page=1
    },
    type(){
        this.getlist()
         this.page=1
    },
    order(){
     this.getlist()
      this.page=1
    }
  }
  ,
  created(){
     this.getlist()
     this.getlist()
     this.getlist()
  }
  ,
  methods: {
     handleCurrentChange(val) {
      
      // 当页码改变的时候就会显示当前的页面
      // console.log(`当前页: ${val}`)
//因为上面的页码值是val，点击哪个页面就会拿到，然后把他拿到的信息在给page这个是当前页码的值 
 this.page = val
      //在调用一下来获取数据跳转后的数据
      this.getlist()
    },toplaylist(id){              //把id就传过去了
this.$router.push('/mv?hzx='+id)
    }
    ,
     getlist(){
      axios({
      url:'https://autumnfish.cn/mv/all',
      method:'get',
      params:{
        area: this.area,
        type: this.type,
        order: this.order,
        limit: this.limit,//这里边的值和上面data一样
        offset:(this.page - 1)*this.limit
      }
    }).then( res => {
      console.log(res)
      this.list=res.data.data
      //处理次数
      for(let i=0;i<this.list.length;i++){//遍历拿到每一项后，然后在判断当前的播放数量playCount是不是大于100000
        if(this.list[i].playCount>100000){//如果大于100000就除以1万，剩余的数取整，最终的值在加一个'w'，
           this.list[i].playCount=parseInt(this.list[i].playCount/10000)+'万'
        }
      }
    })
    }
    }
   
    
    
    
    
  }

</script>

