<template>
  <div class="mv-container">
    <div class="mv-wrap">
      <h3 class="title">mv详情</h3>
      <!-- mv -->
      <div class="video-wrap">
        <!-- autoplay自动播放 -->
        <video
          controls
          autoplay
          :src="url"
        ></video>
      </div>
      <!-- mv信息 -->
      <div class="info-wrap">
        <div class="singer-info">
          <div class="avatar-wrap">
            <img :src="icon" alt="" />
          </div>
          <span class="name">{{mvinfor.artistName}}</span>
        </div>
        <div class="mv-info">
          <h2 class="title">{{mvinfor.briefDesc}}</h2>
          <span class="date">发布：2014-11-04</span>
          <span class="number">播放：{{mvinfor.commentCount}}次</span>
          <p class="desc">
        {{mvinfor.desc}}
          </p>
        </div>
      </div>
      <!-- 精彩评论 -->
      <div class="comment-wrap">
        <p class="title">
          精彩评论<span class="number">
            {{ total }}
          </span>
        </p>
        <div class="comments-wrap">
          <!-- 评论 -->
          <div class="item" v-for="(item, index) in hzx" :key="index">
            <div class="icon-wrap">
              <!-- 头像 -->
              <img :src="item.user.avatarUrl+'?param=50y50'" alt="" />
            </div>
            <div class="content-wrap">
              <div class="content">
                <span class="name">{{ item.user.nickname }}</span>
                <span class="comment">：{{ item.content }}</span>
              </div>
              <!-- 回复 -->
              <div class="re-content" v-if="item.beReplied.length !== 0">
                <span class="name">{{ item.beReplied[0].user.nickname }}</span>
                <span class="comment">：{{ item.beReplied[0].content }}</span>
              </div>
              <!-- <div class="date">{{2020}}</div> -->
            </div>
          </div>
        </div>
      </div>
      <!-- 最新评论 -->
      <div class="comment-wrap">
        <p class="title">
          最新评论<span class="number">({{ total }})</span>
        </p>
        <div class="comments-wrap">
          <!-- 评论 -->
          <div class="item" v-for="(item, index) in hz" :key="index">
            <div class="icon-wrap">
              <!-- 头像 -->
              <img :src="item.user.avatarUrl+'?param=50y50'" alt="" />
            </div>
            <div class="content-wrap">
              <div class="content">
                <span class="name">{{ item.user.nickname }}</span>
                <span class="comment">：{{ item.content }}</span>
              </div>
              <!-- 回复 -->
              <div class="re-content" v-if="item.beReplied.length !== 0">
                <span class="name">{{ item.beReplied[0].user.nickname }}</span>
                <span class="comment">：{{ item.beReplied[0].content }}</span>
              </div>
              <!-- <div class="date">{{ item.time | dateFormat }}</div> -->
            </div>
          </div>
        </div>
      </div>
      <!-- 分页器 -->
      <el-pagination
        @current-change="handleCurrentChange"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="page"
        :page-size="5"
        :limit="limit"
      >
      </el-pagination>
    </div>
    <div class="mv-recommend">
      <h3 class="title">相关推荐</h3>
      <div class="mvs">
        <div class="items">
          <div class="item" v-for="(item,index) in Mvs" :key="index" @click=" toplaylist(item.id)">
            <div class="img-wrap">
              <img :src="item.cover" alt="" />
              <span class="iconfont icon-play"></span>
              <div class="num-wrap">
                <div class="iconfont icon-play"></div>
                <div class="num">{{item.playCount}}</div>
              </div>
              
            </div>
            <div class="info-wrap">
              <div class="name">{{item.name}}</div>
              <div class="singer">{{item.artist}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'mv',
  data() {
    return {
      // 总条数
      total: 20,
      // 页码
      page: 1,
      // 页容量
      limit: 10,
      url:"",//因为就只有一个就不需要遍历循环就可以直接拿url
   Mvs:[],
   mvinfor:"",
   icon:'',
   hzx:'',//热门评论
   hz:''//最新
   
   };
  },
  methods: {
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`);
    },toplaylist(id){              //把id就传过去了
this.$router.push('/mvv?hzx='+id)
    }
  },
  created(){
    //获取mv播放地址，
    axios({
      url:'https://autumnfish.cn/mv/url',
      method:'get',
      params:{
        id:this.$route.query.hzx
      }
    }).then(res=>{
      // console.log(res)
      this.url=res.data.data.url
    })
    // //获取mv相关
     axios({
      url:'https://autumnfish.cn/simi/mv',
      method:'get',
      params:{
        mvid:this.$route.query.hzx
      }
    }).then(res=>{
      // console.log(res)
      this.Mvs=res.data.mvs
    })
    //mv信息
     axios({
      url:'https://autumnfish.cn/mv/detail',
      method:'get',
      params:{
        mvid:this.$route.query.hzx
      }
    }).then(res=>{
      console.log(res)
      this.mvinfor=res.data.data
      for(let i=0;i<this.Mvs.length;i++){//遍历拿到每一项后，然后在判断当前的播放数量playCount是不是大于100000
        if(this.Mvs[i].playCount>100000){//如果大于100000就除以1万，剩余的数取整，最终的值在加一个'w'，
           this.Mvs[i].playCount=parseInt(this.Mvs[i].playCount/10000)+'万'
        }
      }
      axios({//因为这个mv信息里没有找到歌手的图片，然后就在接着mv信息里面在调用axios找到歌手的图片，然后在放到页面上
        url:'https://autumnfish.cn/artists',
        method:'get',
        params:{//因为歌手信息里面需要他的id才能获取到歌手信息，就在传一个mv里的歌手id
          id:this.mvinfor.artists[0].id
        }
      }).then(res=>{
        // console.log(res)
        //    console.log(1)
        this.icon=res.data.artist.picUrl
      })
    })
    // 获取评论数据
    axios({
      url:'https://autumnfish.cn/comment/mv',
      method:'get',
      params:{id:this.$route.query.hzx,
      limit:10,
      offset:0}
      
    }).then(res=>{
      // console.log(1)
      // console.log(res)
      this.hzx=res.data.comments
      this.hz=res.data.hotComments
    })
  }
};
</script>

<style></style>
