<template>
  <div class="top-container">
    <div class="left-box">
      <div class="icon-wrapper">
        <span class="iconfont icon-home"></span>
        <span class="iconfont icon-sami-select"></span>
        <span class="iconfont icon-full-screen"></span>
      </div>
      <div class="history-wrapper">
        <span class="iconfont icon-arrow-lift" onClick="javascript :history.back(-1)"></span>
        <span class="iconfont icon-arrow-right" onClick="javascript :history.back(+1)"></span>
      </div>
    </div>
    <div class="right-box">
      <div class="el-input el-input--small el-input--prefix">
        <input
          type="text"
          autocomplete="off"
          placeholder="搜索"
          class="el-input__inner"
          v-model="inputValue"
          @keyup.enter="toResult"
        />
        <span class="el-input__prefix">
          <i class="el-input__icon el-icon-search"></i>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'top',
    data() {
      return {
        //双向绑定输入的内容
        inputValue: '薛之谦'
      }
    },
    methods:{
      toResult(){
        //先判断用户输入的内容是不是空的，不然的话就不能访问，就会弹出一个提示框
        if (this.inputValue==''){
          //这是elment ui的提示
          this.$message.warning('哎呀天啦噜还没输入内容呢')
        }else{
  // 否者就就执行这下面的  ：去搜索页面 携带数据
// 就给当前的路由添加一个路径为搜索的页面“路由里面？就是添加”然后在添加一个当前输入框里的内容
  this.$router.push('/result?name='+this.inputValue)
        }
      }
    }
  }
</script>

<style scoped></style>
