<template>
  <div class="result-container">
    <div class="title-wrap">
      <h2 class="title">{{$route.query.name}}</h2>
      <span class="sub-title">这么多结果你也看不完共{{count}}个</span>
    </div>
    <el-tabs v-model="activeIndex">
      <el-tab-pane label="歌曲" name="songs">
        <table class="el-table">
          <thead>
            <th></th>
            <th>音乐标题</th>
            <th>歌手</th>
            <th>专辑</th>
            <th>时长</th>
          </thead>
          <tbody>
            <tr class="el-table__row" v-for="(item,index) in songList" :key="index"
            @dblclick="playMusic(item.id)" >
              <td>{{index+1}}</td>
              <td>
                <div class="song-wrap">
                  <div class="name-wrap">
                    <span>{{item.name}}</span>
                    <!-- mv的图标不是每个都会有mv的标签名的显示的，只有mvid不等于0才会显示 -->
                    <span class="iconfont icon-mv" v-if="item.mvid!=0"></span>
                  </div>
                  <!-- 这里要判断下，因为有的歌曲没有二级标题，可以在alias里面判断他的长度有没有大于1就是不等于与0的就显示 -->
                  <span v-if="item.alias.length!=0">{{item.alias[0]}}</span>
                </div>
              </td>
              <td>{{item.artists[0].name}}</td>
              <td>{{item.album.name}}</td>
              <td>{{ item.duration}}</td>
            </tr>


          </tbody>
        </table>
      </el-tab-pane>
      <el-tab-pane label="歌单" name="lists">
        <div class="items">
          <div v-for="(item,index) in playlists" :key="index" class="item" @click="toplaylist(item.id)" >
            <div class="img-wrap">
              <div class="num-wrap">
                播放量:
                <span class="num">{{item.playCount}}</span>
              </div>
              <img :src="item.coverImgUrl" alt="" />
              <span class="iconfont icon-play"></span>
            </div>
            <p class="name">{{item.name}}</p>
          </div>
        
        </div>
      </el-tab-pane>
      <el-tab-pane label="MV" name="mv">
        <div class="items mv">
          <div class="item" v-for="(item,index) in mv" :key="index"  @click="toMV(item.id)">
            <div class="img-wrap">
              <img :src="item.cover" alt="" />
              <span class="iconfont icon-play"></span>
              <div class="num-wrap">
                <div class="iconfont icon-play"></div>
                <div class="num">{{item.playCount}}</div>
              </div>
              <span class="time">{{item.duration}}</span>
            </div>
            <div class="info-wrap">
              <div class="name">{{item.name}}</div>
              <div class="singer">{{item.artistName}}</div>
            </div>
          </div>
         
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'result',
  data() {
    return {
      //切换时会改变的数据
      activeIndex: 'songs',
      //歌曲列表
      songList:[],
      // 歌单列表
      playlists:[],
      // 搜索到的总数
      // mv的总数
      mv:[],
      count:'',
 
    }
  },
  watch:{
    
    activeIndex(){
      let type = 1;
      let limit =10;
      // switch 语句用于基于不同条件执行不同动作。
     switch (this.activeIndex) {
       case 'songs':
         type = 1;
         limit=30;
         break;
      case 'lists':
         type =1000;
         limit=16;
         break;
          case 'mv':
         type = 1004;
         limit=16;
         break;
       default:
         break;
     }
       axios({
      url: 'https://autumnfish.cn/search',
      method:'get',
      params:{
        keywords:this.$route.query.name,
        type,
        limit
      }
    }).then(res =>{
      console.log(res)
      if(type==1){
        this.songList=res.data.result.songs
      for (let i = 0;i < this.songList.length; i++) {
           let duration=this.songList[i].duration
          let min = parseInt(this.songList[i].duration / 1000 / 60)
           let sec = parseInt((this.songList[i].duration / 1000) % 60)
           if (min < 10) {
            min = '0' + min
          }
          if(sec<10){
            sec = '0'+ sec
          }
         this.songList[i].duration=min+':'+sec
      }
      this.count=res.data.result.songCount
      }else if(type==1000){
        // //这是当type等于歌单的操作
        //歌单的逻辑,因为他们的接口地址还是在一个地方，就只是他的搜索类型变了，新得搜索类型就要从新挂载到页面上
        this.playlists = res.data.result.playlists
        // 总数
        this.count = res.data.result.playlistCount
        //判断每一个的播放次数是否大于100000十万，如果大于就除以1万，在取整数，然后在后面加一个万
      for(let i=0;i<this.playlists.length;i++){
         if(this.playlists[i].playCount>100000){//playCount是当前歌曲播放总数
          this.playlists[i].playCount=parseInt (this.playlists[i].playCount/10000)+'万'
    //判断如果每一项的播放次数大于100000就给他设置除以一万，在后面在家一万，最后取整
      }}
      }else{
          console.log(res)
          // 给mv赋值
          this.mv=res.data.result.mvs
          //这是搜索结果
          this.count=res.data.result.mvCount
          for(let i=0;i<this.mv.length;i++){
            let min=parseInt(this.mv[i].duration/1000/60)
            let sec=parseInt(this.mv[i].duration/1000%60)
            if(min<10){
              min='0'+min
            }
            if(sec<10){
              sec='0'+sec
            }
            this.mv[i].duration=min+':'+sec
          }
          for(let i=0;i<this.mv.length;i++){
            if(this.mv[i].playCount>100000){
              this.mv[i].playCount=parseInt(this.mv[i].playCount/10000)+'万'
            }
            
            
          }
          
      }
      
     
   }) 
    }
  }
  ,
  methods:{
//跳转到mv页面详情页面
toMV(id){
  this.$router.push('./mv?hzx='+id)
},
    //跳转到歌单详情页面，并把id带上，因为你当前的接口不知道是哪一个，所以添加id就会跳转
    toplaylist(id){              //把id就传过去了
this.$router.push('/playlist?hzx='+id)
    } 
    ,
    playMusic(id){
     // console.log(id)https://autumnfish.cn/song/url?1456023148
       //点击一下就触发axios的执行事件，
       axios({///这个播放接口需要传id，给网站后面添加id，然后不一样的歌点后就会播放不一样的地址
         url:'https://autumnfish.cn/song/url',
         method:'get',
         params:{
           id:id//添加id到网站，两个id一样的话就可以只写一个
         }
       }).then(res => {
         //用一个参数来接收播放地址，然后在修改父组件里面的播放地址
          // console.log(res)//这里就可以看到播放地址了
          let url = res.data.data[0].url
          // console.log(this.$parent.musicUrl)
          //设置给父组件的播放地址
          this.$parent.musicUrl = url
       })
    }
  },
    created(){
    axios({
      url:'https://autumnfish.cn/search',
      method:'get',
      params:{
        keywords:this.$route.query.name,//这个是必选的参数的关键词，就是搜索的关键字
        limit:18,
        // offset:(page-1)*limit,
        type:1//歌曲是1，歌单值是1000，mv是1004,type写了固定的这里就不能写死了，可以写type：type，这样就可以简写type
        ,
      }
    }).then(res =>{
      // console.log(res)
      
      this.songList=res.data.result.songs
      for (let i = 0;i < this.songList.length; i++) {
           let duration=this.songList[i].duration
          let min = parseInt(this.songList[i].duration / 1000 / 60)
           let sec = parseInt((this.songList[i].duration / 1000) % 60)
           if (min < 10) {
            min = '0' + min
          }
          if(sec<10){
            sec = '0'+ sec
          }
         this.songList[i].duration=min+':'+sec
      }
      this.count=res.data.result.songCount
     //这是当type等于歌单的操作
   })
  }
};
</script>

