<template>
  <div class="playlists-container">
    <!-- 同步 -->
    <div class="top-card">
      <div class="icon-wrap">
        <!-- 封面 -->
        <img :src="topList.coverImgUrl" alt="" />
      </div>
      <div class="content-wrap">
        <div class="tag">精品歌单</div>
        <!-- 标题 -->
        <div class="title">
         {{topList.name}}
        </div>
        <!-- 介绍 -->
        <div class="info">
          {{topList.description}}
        </div>
      </div>
      <!-- 背景 -->
      <img :src="topList.coverImgUrl" alt="" class="bg" />
      <div class="bg-mask"></div>
    </div>

    <div class="tab-container">
      <!-- tab栏 顶部 -->
      <div class="tab-bar">
        <!-- active设置了样式class设置在哪里哪里就会亮 -->
        <!-- 如果当前的active里面tag里面的值等于当前这行的值 但是现在默认的tag值是全部，所以全部就会亮，然后在这里就设置一个点击事件把tag的值改为当前-->
        <span class="item" :class="{active:tag=='全部'}" @click="tag='全部'">全部</span>
        <span class="item" :class="{active:tag=='欧美'}" @click="tag='欧美'">欧美</span>
        <span class="item" :class="{active:tag=='华语'}" @click="tag='华语'">华语</span>
        <span class="item" :class="{active:tag=='流行'}" @click="tag='流行'">流行</span>
        <span class="item" :class="{active:tag=='说唱'}" @click="tag='说唱'">说唱</span>
        <span class="item" :class="{active:tag=='摇滚'}" @click="tag='摇滚'">摇滚</span>
        <span class="item" :class="{active:tag=='民谣'}" @click="tag='民谣'">民谣</span>
        <span class="item" :class="{active:tag=='电子'}" @click="tag='电子'">电子</span>
      <span class="item" :class="{active:tag=='轻音乐'}" @click="tag='轻音乐'">轻音乐</span>
    <span class="item" :class="{active:tag=='影视原声'}" @click="tag='影视原声'">影视原声</span>
         <span class="item" :class="{active:tag=='ACG'}" @click="tag='ACG'">ACG</span>
        <span class="item" :class="{active:tag=='怀旧'}" @click="tag='怀旧'">怀旧</span>
        <span class="item" :class="{active:tag=='治愈'}" @click="tag='治愈'">治愈</span>
        <span class="item" :class="{active:tag=='旅行'}" @click="tag='旅行'">旅行</span>
      </div> 
      <!-- tab的内容区域 -->
      <div class="tab-content">
        <div class="items">
          <div v-for="(item,index) in hzx" :key="index" class="item">
            <div class="img-wrap"  @click=" toplaylist(item.id)" >
              <div class="num-wrap">
                播放量:
                <span class="num">{{item.playCount}}</span>
              </div>
              <img :src="item.coverImgUrl" alt="" />
              <span class="iconfont icon-play"></span>
            </div>
            <p class="name">{{item.name}}</p>
          </div>
         
        </div>
      </div>
    </div>
    <!-- 分页器
    total 总条数
    current-page 当前页等于了一个值，这个值在data的page里，
    只要给data设置了页面为多少的数字，那当前页面就是多少，因为下面的点击事件可以获取当前点击的页面值信息，所以直接就可以把获取到的值赋值给tage
    page-size每页多少条数据
    @current-change 页码改变事件
     -->
     <!--el-pagination作用当数据量过多时,使用分页分解数据  -->
    <el-pagination 
      @current-change="handleCurrentChange" 
      background
     
      :total="total"
      :current-page="page"
      :page-size="10"
    >
    </el-pagination>
    
  </div>
</template>

<script>
// 导入axios
import axios from 'axios'
export default {
  name: 'recommend',
  data() {
    return {
      // 总条数
      total:0,
      // 当前页
      page:1,
      
      topList:{},
      hzx:[],
      // 当前选择的分类，默认
      tag:"欧美"
    };
  },
 watch:{
tag(){
  // 顶部的精品歌单
    this.topData()
    ,//获取的是歌单列表
    this.listData()
    this.page=1
//如果在顶部的标签里面点击一下，他会有的效果是当前高亮，还有设置他的当前页面为第一页
}
 },
    
  methods: {
    toplaylist(id){              //把id就传过去了
this.$router.push('/playlist?hzx='+id)
    }
  ,
  //把写在生命周期里的内容在mothods里封装成方法
    // 抽取的方法1顶部的数据
    topData(){
      axios({
      url:'https://autumnfish.cn/top/playlist/highquality',
      method:'get',
      params:{
        limit:1,
       // cat:'全部'//因为调用数据的时候，分类列表是写死的，所以要把他改为动态的this.tag,tag是多少他的cat分类就自动设置多少
      cat:this.tag
      }
    }).then(res =>{
      // 这个是直接把获取到的值给toplist，它里面就有值了然后直接把里面的值在标签里使用
       this.topList=res.data.playlists[0]
      //  console.log(res)
    })
    },
    // 抽取的方法2列表的数据
    listData(){
       axios({
      url:'https://autumnfish.cn/top/playlist/',//可选的参数limit：获取的个数/offset：偏移数量，用于分页/cat：歌单标签
      mothod:'get',
     params:{
        limit: 10,
      //然后获取到了页码，在从这里来跳转页面，公式就是当前获取到的页面-1页在乘以limit每一页多少条总数
     offset:(this.page-1)*10,
    //cat:'全部'
      cat: this.tag
     }
    }).then(res => {
       console.log(res)
      this.hzx=res.data.playlists
      this.total=res.data.total//设置下面导航条total的总数，之前data那里的tatal设置的是0，现在就是res.total的总数了
      for(let i=0;i<this.hzx.length;i++){//遍历拿到每一项后，然后在判断当前的播放数量playCount是不是大于100000
        if(this.hzx[i].playCount>100000){//如果大于100000就除以1万，剩余的数取整，最终的值在加一个'w'，
           this.hzx[i].playCount=parseInt(this.hzx[i].playCount/10000)+'万'
        }
      }
    
    })
    },
    // 页码改变事件，
    handleCurrentChange(val) {
      // 当页码改变的时候就会显示当前的页面
      // console.log(`当前页: ${val}`)
//因为上面的页码值是val，点击哪个页面就会拿到，然后把他拿到的信息在给page这个是当前页码的值 
 this.page = val
      //在调用一下来获取数据跳转后的数据
      this.listData()
    }
  },
  created(){//在生命周期这里直接调用方法就好了
    // 顶部的精品歌单
    this.topData()
    ,//获取的是歌单列表
    this.listData()
    
  }
};
</script>

